@media only screen and (max-width: 990px) { .footer__box { padding: 15px; } }
.footer__label, .footer__form { float: left; }
.footer__label { color: #282828; font-size: 30px; line-height: 1; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 500; width: 51%; padding: 10px 0; }
@media only screen and (max-width: 1320px) { .footer__label { font-size: 24px; width: 55%; padding: 13px 0 13px 5px; } }
@media only screen and (max-width: 990px) { .footer__label { width: 100%; font-size: 22px; padding: 0 0 10px; } }
@media only screen and (max-width: 660px) { .footer__label { text-align: center; font-size: 18px; } }
.footer__form { width: 49%; position: relative; }
@media only screen and (max-width: 1320px) { .footer__form { width: 45%; } }
@media only screen and (max-width: 990px) { .footer__form { width: 100%; } }
@media only screen and (max-width: 660px) { .footer__form { text-align: center; } }
.footer__form i { position: absolute; top: 12px; left: 17px; font-size: 25px; color: #9bc1bc; }
.footer__btn { position: absolute; top: 5px; right: 5px; width: 210px; background-color: #f7b153; font-size: 18px; line-height: 1; color: #fff; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 400; padding: 12px 10px 10px; display: inline-block; }
@media only screen and (max-width: 1320px) { .footer__btn { width: 150px; } }
@media only screen and (max-width: 990px) { .footer__btn { width: 210px; } }
@media only screen and (max-width: 660px) { .footer__btn { width: 150px; } }
@media only screen and (max-width: 480px) { .footer__btn { width: 100%; max-width: 250px; position: relative; top: auto; right: auto; } }
.footer__input { font-size: 17px; color: #282828; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 400; font-style: italic; padding: 16px 230px 14px 50px; width: 100%; }
@media only screen and (max-width: 1320px) { .footer__input { padding-right: 170px; } }
@media only screen and (max-width: 990px) { .footer__input { padding-right: 230px; } }
@media only screen and (max-width: 660px) { .footer__input { padding-right: 170px; } }
@media only screen and (max-width: 480px) { .footer__input { padding-right: 20px; margin-bottom: 15px; } }
.footer__input::placeholder { color: #282828; opacity: 1; }
.footer__social { font-size: 0; float: left; }
@media only screen and (max-width: 660px) { .footer__social { float: none; text-align: center; margin-bottom: 20px; } }
.footer__social li { display: inline-block; vertical-align: top; padding: 0 26px 0 12px; }
@media only screen and (max-width: 660px) { .footer__social li { padding: 0 12px; } }
.footer__social a { color: #fff; display: block; }
.footer__social a i { font-size: 36px; }
.footer__copy { float: right; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 300; line-height: 1; font-size: 0; padding: 2px 0 0; }
@media only screen and (max-width: 660px) { .footer__copy { float: none; text-align: center; } }
.footer__copy p { color: #fff; padding: 10px 30px 8px 0; }
.footer__copy p, .footer__age { vertical-align: top; display: inline-block; font-size: 22px; }
.footer__age { color: #857e7b; padding: 11px 0 7px; text-align: center; width: 40px; background: #fff; border-radius: 4px; }

.square { width: 13px; height: 13px; background: #9bc1bc; display: inline-block; }

.text-big { font-size: 24px; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 500; }
.text-big_md { font-size: 20px; }

.section-title { font-size: 18px; letter-spacing: 2.6px; color: #9bc1bc; margin-bottom: 20px; }

.meta { font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 400; font-size: 11px; color: #b7b7b7; }

.divider { height: 1px; margin: 20px 0; background: #9bc1bc; }

.l-news { *zoom: 1; }
.l-news:after { content: " "; display: table; clear: both; }
.l-news .l-col1 { padding-right: 15px; float: left; width: 66.67%; width: calc(100% - (100% / 3)); }
.l-news .l-col2 { float: right; padding-left: 15px; padding-right: 110px; width: 33.33%; width: calc(100% / 3); }

.soc-wrapper { padding-left: 110px; position: relative; }
.soc-wrapper .soc-v { position: absolute; left: 0; top: 0; }

.soc-v > li { text-align: center; margin-bottom: 1px; display: block; }
.soc-v > li a { font-size: 29px; color: #fff; padding: 12px 10px; background: #9bc1bc; width: 50px; height: 50px; transition: background 0.3s; }
.soc-v > li a.fa-envelope { font-size: 24px; }
.soc-v > li a.icon-vk { font-size: 24px; }
.soc-v > li a:hover { background: #7cada7; }

.news-header h1, .news-header .h1 { margin-bottom: 20px; }
.news-header .text-big { margin-bottom: 40px; }

.news-line { margin-bottom: 40px; }
.news-line .btn { display: inline-block; vertical-align: middle; margin-right: 20px; margin-left: -5px; }
.news-line__info { display: inline-block; vertical-align: middle; color: #757575; }
.news-line__info span { font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 300; padding-left: 10px; }

.banner-img { padding: 45px 25px; border: 1px solid #e6e6e6; text-align: center; margin-bottom: 20px; background: #fff; }
.banner-img a { line-height: 0; }
.banner-img img { max-width: 100%; }

.item-sm { font-size: 16px; padding-bottom: 15px; margin-bottom: 15px; border-bottom: 1px solid #e6e6e6; font-family: "Fira Sans", Helvetica, Arial, sans-serif; font-weight: 300; }
.item-sm:last-child { border: 0; }
.item-sm p { line-height: 1.2; }
.item-sm .text-big { margin-bottom: 10px; line-height: 1; }
.item-sm:hover .text-big { text-decoration: underline; }


